.App {
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  font-family: 'Times New Roman', Times, serif;
  display: flex;
}

button:hover {
  cursor: pointer;
}
.Menu {
  background-color:lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 500px;
  border-radius: 10px;
}
.Menu input, button {  
  border: none;
  border-radius: 5px;
  font-size: 20px;
  width: 300px;
  height: 50px;
  margin: 5px;
}
.Quiz {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.EndScreen {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.Menu, .Quiz, .EndScreen h1 {
  color: white;
}

#nextQuestion {
  width: 200px;
}

h3 {
  color: #ffffff;

}